body {
  background: url(/curriculum_vitae/static/media/palm-leaf.1458ca16.png)
}

.card{
  background-color: rgba(231, 231, 241, 0.432) !important;
}

.card-title {
  background-color: rgb(199, 174, 223);
  padding: 10px 10px;
  width: 100%;
  font-family: 'ZCOOL XiaoWei', serif;
}

p, a {
  font-family: 'Jura', sans-serif;
  font-size: 20px;
}

h3, h4, h5 {
  font-family: 'Jura', sans-serif;
  font-size: 20px;
  font-weight: bold !important;
}

.person, .formation, .solo {
  display: -webkit-flex;
  display: flex;
}

@media only screen and (max-width: 1024px) {
  .person, .formation, .solo {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
}



@media only screen and (max-width: 768px) {
  .person, .formation, .solo {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .card-title {
    font-size: 20px
  }

  p, a {
    font-size: 15px
  }

  h3 {
    font-size: 17px !important;
  }

  h4 {
    font-size: 15px !important;
  }

  h5 {
    font-size: 13px !important;
  }
}

